@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap");
.container {
  background: #E5E5E5;
  width: 100%;
  min-height: 100vh;
}
.container .first-section {
  background: #E5E5E5;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 60px;
  position: absolute;
  top: 96px;
}
@media (max-width: 1024px) {
  .container .first-section {
    flex-direction: column;
  }
}
@media (max-width: 540px) {
  .container .first-section {
    padding: 20px;
  }
}
.container .first-section .first-section__img {
  width: 505px;
  height: 303px;
}
@media (max-width: 540px) {
  .container .first-section .first-section__img {
    width: 100%;
    margin: 20px;
  }
}
.container .first-section .first-section__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 40px;
}
@media (max-width: 1024px) {
  .container .first-section .first-section__content {
    align-items: center;
  }
}
.container .first-section .first-section__content .first-section__heading {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 42px;
  line-height: 50.4px;
  color: #121212;
}
.container .first-section .first-section__content .first-section__description {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  max-width: 380px;
}
@media (max-width: 1024px) {
  .container .first-section .first-section__content .first-section__description {
    text-align: justify;
  }
}
@media (max-width: 540px) {
  .container .first-section .first-section__content .first-section__description {
    padding: 15px;
  }
}
.container .first-section .first-section__content .first-section__btn {
  font-weight: 700;
  font-size: 16px;
  line-height: 16px;
  color: #FFFFFF;
  background-color: #E4C1AE;
  padding: 16px 40px;
  border-radius: 32px;
  border: 2px solid #E4C1AE;
  cursor: pointer;
}
.container .first-section .first-section__content .first-section__btn:hover {
  background-color: transparent;
  color: #E4C1AE;
}

.modal {
  visibility: hidden;
}
.modal .modal__bg {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(18, 18, 18, 0.5);
  z-index: 99;
}
.modal .modal__close {
  position: absolute;
  z-index: 999;
  top: 40px;
  right: 40px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.modal .modal__content {
  background: #FFFFFF;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -45%);
  z-index: 999;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;
  gap: 20px;
  min-height: 200px;
  width: 60%;
  padding: 50px 60px;
  opacity: 0;
  transition: all 0.4s ease-in-out;
}
@media (max-width: 768px) {
  .modal .modal__content {
    width: 80%;
  }
}
@media (max-width: 540px) {
  .modal .modal__content {
    width: 90%;
    align-items: center;
    padding: 0 20px;
  }
}
.modal .modal__content .modal__heading {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 42px;
  line-height: 50.4px;
}
.modal .modal__content .modal__description {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
}
.modal .modal__content .modal__reset-clicks {
  visibility: hidden;
  font-weight: 700;
  font-size: 16px;
  line-height: 16px;
  color: #FFFFFF;
  background-color: #E4C1AE;
  padding: 16px 40px;
  border-radius: 32px;
  border: 2px solid #E4C1AE;
  cursor: pointer;
}
.modal .modal__content .modal__reset-clicks:hover {
  background-color: transparent;
  color: #E4C1AE;
}
.modal .modal__content .modal__reset-clicks.active-reset-btn {
  visibility: visible;
}
.modal .modal__content .modal__table {
  width: 100%;
  color: #121212;
  display: block;
  overflow-y: scroll;
}
.modal .modal__content .modal__table tr:nth-child(odd) {
  background-color: #E4C1AE;
}
.modal.active {
  visibility: visible;
}
.modal.active .modal__content {
  opacity: 1;
  transform: translate(-50%, -50%);
}
.modal .loader {
  display: flex;
  justify-content: center;
}
.modal .loader::after {
  content: "";
  width: 50px;
  height: 50px;
  border: 10px solid #E4C1AE;
  border-top-color: #121212;
  border-radius: 50%;
  animation: spinning 1s linear infinite;
}

@keyframes spinning {
  to {
    transform: rotate(1turn);
  }
}
*, *::before, *::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}